.contacts {
  position: relative;
  margin-top: em(150, $fz_base);

  &::before {
    content: '';
    position: absolute;
    top: -50%;
    left: 0;
    width: 100%;
    height: 150%;
    background: linear-gradient(30deg, transparent 50%, #fff 45%),
    linear-gradient(to top, #efecf0, #c5e4fc);
    z-index: -1;
  }
}

.contacts-card {
  $self: &;

  position: relative;
  display: flex;
  border-radius: em(10, $fz_base);
  box-shadow: em(0 0 20 5, $fz_base) rgba(#aeaeae, .4);
  background-color: #fff;

  @include screen(lg,max) {
    flex-direction: column;
  }

  &__decor {
    position: absolute;
    right: em(-50, $fz_base);
    top: em(-120, $fz_base);
    z-index: 1;
    width: em(140, $fz_base);
    height: em(133, $fz_base);

    img {
      @include fill-img;
    }
  }

  &__address {
    padding: em(40 35, $fz_base);
    flex-grow: 1;
  }

  &__map {
    width: 40%;
    position: relative;
    overflow: hidden;

    @include screen(lg,max) {
      width: 100%;
      height: em(500, $fz_base);
    }
  }

  &__email {
    color: #89919a;
    display: block;
    text-align: right;
    margin-top: em(30, $fz_base);

    #{$self}__label {
      $fz: 14;
      font-size: em($fz, $fz_base);

      &--brend {
        color: #0a6ed1;
      }
    }
  }

  &__iframe {
    border: 0;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    min-width: 100%;
    min-height: 100%;
  }
}

.contacts-card-list {
  $self: &;
  overflow-y: auto;
  max-height: 100%;

  &__item {
    display: flex;
    flex-direction: column;
    background-color: rgba(#565d64, .1);
    padding: em(20 25, $fz_base);
    border-radius: em(10, $fz_base);
    color: #89919a;
    fill: #89919a;

    &:not(:first-child) {
      margin-top: em(10, $fz_base);
    }
  }

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: em(30, $fz_base);
    color: #247dd6;

    #{$self}__info {
      font-weight: 500;

      #{$self}__label {
        $fz: 16;
        font-size: em($fz, $fz_base);
      }
    }
  }

  &__icon {
    width: em(15, $fz_base);
    height: em(15, $fz_base);
    margin-right: em(10, $fz_base);
  }

  &__label {
    $fz: 14;
    font-size: em($fz, $fz_base);
  }

  &__info {
    display: flex;
    align-items: center;

    &:not(:first-child) {
      margin-top: em(10, $fz_base);
    }
  }

  &__mark {
    color: $color-light;
    padding: em(5 10, $fz_base);
    border-radius: em(5, $fz_base);
    background-color: #89919a;

    &--main {
      background-color: #247dd6;
    }

    #{$self}__label {
      $fz: 12;
      font-size: em($fz, $fz_base);
    }
  }
}

.contacts-form {
  $self: &;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: #272f4a;
  height: 100%;
  overflow: hidden;

  &__title {
    #{$self}__label {
      $fz: 24;
      font-size: em($fz, $fz_base);
    }
  }

  &__item {
    position: relative;
    margin-top: em(20, $fz_base);
    color: #89919a;
  }

  &__border {
    border: 2px solid #89919a;
    height: em($height-input, $fz_base);
    border-radius: em(3, $fz_base);
    transition: border-color $ease-main;
  }

  &__placeholder {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      width: 100%;
      height: 2px;
      font-size: 0;
      line-height: 0;
      background-color: #89919a;
    }


    #{$self}__label {
      transition: transform $ease-main;
      display: inline-block;
      transform: translateY(em($height-input / 2.5, $fz_base));
    }
  }

  &__input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    padding: em(0 $padding-left-input, $fz_base);
    color: inherit;
    $fz: 18;
    font-size: em($fz, $fz_base);

    &:focus {
      + #{$self}__border {
        border-color: $color-brend;

        #{$self}__placeholder::before {
          display: none;
        }

        #{$self}__label {
          transform: translateY(0);
          color: $color-brend;
        }
      }
    }
  }

  &__descr {
    @include screen(lg,max) {
      margin: em(20 0, $fz_base);
    }

    #{$self}__label {
      $fz: 14;
      font-size: em($fz, $fz_base);
    }
  }

  &__btn {
    @include screen(lg,min) {
      align-self: flex-start;
    }

    @include screen(md,max) {
      @include screen(xs,min) {
        max-width: 78%;
      }
    }
  }
}
