.group {
  margin-top: em(150, $fz_base);
}

.group-card {
  position: relative;
  box-shadow: 0 2px 40px 0 #f1f2f4;
  border-radius: em(10, $fz_base);
  padding: em(35 25, $fz_base);
  background-color: #fff;
  
  &__decor {
    position: absolute;
    left: em(-80, $fz_base);
    top: em(60, $fz_base);
    z-index: -1;
    width: em(180, $fz_base);
    height: em(200, $fz_base);
    transform: rotate(-30deg) scaleX(-1);

    img {
      @include fill-img;
    }
  }

  &__title {
    $fz: 24;
    font-size: em($fz, $fz_base);
    color: $color-brend;
    margin-bottom: em(30, $fz);
  }

  &__table {
    padding-left: em(10, $fz_base);
  }
}

.group-card-table {
  @include screen(xs,max) {
    overflow-x: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      width: 0;
      height: 0;
      background: transparent;
    }
  }

  &__title {
    $fz: 18;
    font-size: em($fz, $fz_base);
    font-weight: 500;
    
  }

  &__descr {
    $fz: 14;
    font-size: em($fz, $fz_base);
    display: inline-block;
    margin-top: em(5, $fz);
    color: #aeaeae;
  }

  &__date {
    $fz: 14;
    font-size: em($fz, $fz_base);
    color: #3b5064;
  }

  &__row {
    position: relative;
    display: inline-flex;
    align-items: center;
    min-width: 100%;  

    &--header {
      + div {
        margin-top: em(20, $fz_base);
      }
    }

    &:not(:first-child) {
      border-bottom: 1px solid rgba(#aeaeae, .5); 
    }
  }

  &__cell {
    display: flex;
    flex-direction: column;
    color: #354a5f;
    width: calc(100 / 3 * 1%);
    padding: em(10 0, $fz_base);

    @include screen(xs,max) {
      width: em(250, $fz_base);
      flex-shrink: 0;
    }
  }
}