.intro {
  position: relative;
  background: linear-gradient(108deg, #340750 33%, #27063b 67%);
  padding-top: em(110, $fz_base);
  padding-bottom: em(85, $fz);

  &__video {
    @include fill-img;
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    z-index: 3;
  }

  &__header {
    padding: em(30 20 0 , $fz);
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    line-height: 1.4;
  }

  &__link {
    $fz: 24;
    font-size: em($fz, $fz_base);
    color: $color-brend;
    font-weight: 700;    
  }

  &__adress {
    color: $color-light;
    $fz: 17;
    font-size: em($fz, $fz_base);
  }

  &__promo {
    margin-top: auto;
    padding-top: em(70, $fz_base);

    @include screen(lg,max) {
      display: none;
    }
  }

  &__title,
  &__descr {
    margin-left: em(-45, $fz_base);
  }

  &__descr {
    margin-top: em(30, $fz_base);
  }
}

.intro-action {
  position: relative;
  max-width: em(355, $fz);
  margin-left: auto;
  color: $color-light;

  &__text {
    $fz: 24;
    font-size: em($fz, $fz_base);
    color: hsl(90, 69%, 72%)
  }

  &__link {
    position: absolute;
    right: 0;
    bottom: em(-20, $fz);
    display: inline-block;
    padding: em(5 10, $fz);
    background-color: hsl(353, 98%, 41%);
    border-radius: 3px;
  }
}

.intro-title {
  display: flex;
  text-transform: uppercase;

  &__num {
    color: hsl(353, 98%, 41%);
    $fz: 100;
    font-size: em($fz, $fz_base);
    line-height: 0.75;
    margin-bottom: em(-5, $fz);
    font-family: $font-third;
    font-weight: 700;
  }

  &__descr {
    display: flex;
    flex-direction: column;
    height: auto;
    justify-content: space-between;
    color: $color-light;
    line-height: 1.15;
    font-family: $font-main;
  }

  &__thin {
    $fz: 9;
    font-size: em($fz, $fz_base);
    text-transform: lowercase;
    font-family: $font-main;
  }

  &__main {
    $fz: 18;
    font-family: $font-main;
    font-size: em($fz, $fz_base);
    font-weight: 900;
  }

  &__subtitle {
    $fz: 10;
    font-size: em($fz, $fz_base);
    font-family: $font-third;
    font-weight: 400;
    text-transform: lowercase;
  }
}

.intro-descr {
  text-transform: uppercase;
  color: #f5a623;

  &__label {
    $fz: 16;
    font-size: em($fz, $fz_base);
  }
}

.logo {
  position: relative;
  display: inline-block;
  width: em(125, $fz);
  height: em(36, $fz);

  &__picture,
  img {
    @include fill-img;
  }
}

.video {
  &__item {
    @include fill-img;
  }
}
