.footer {
  $self: &;

  padding: em(40 0 50, $fz_base);
  background: #efecf0;
  margin-top: auto;

  &__decor {
    position: absolute;
    right: em(-90, $fz_base);
    bottom: em(35, $fz_base);
    z-index: 1;
    width: em(90, $fz_base);
    height: em(90, $fz_base);

    img {
      @include fill-img;
    }
  }

  &__container {
    position: relative;
    
    @include screen(md,max) {
      flex-wrap: wrap;
      margin-left: 0;

      @include screen(xs,min) {
        max-width: 80%;
      }
    }
  }

  &__copyright {
    color: #89919a;

    @include screen(md,max) {
      width: 100%;
    }

    #{$self}__label {
      $fz: 14;
      font-size: em($fz, $fz_base);
    }  
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100px;
    width: em(45, $fz_base);
    height: em(45, $fz_base);
    fill: $color-brend;
    border: 1px solid $color-brend;
    transition: color $ease-main, border-color $ease-main;

    &:hover {
      border-color: lighten($color-brend, 10%);
      fill: lighten($color-brend, 10%);
    }
  }

  &__icon {
    width: em(25, $fz_base);
    height: em(25, $fz_base);
    transition: fill $ease-main;
  }
}

.footer-nav {
  display: flex;
  align-items: center;
  color: #5a78a0;

  &__item {
    &:not(:first-child) {
      margin-left: em(40, $fz_base);
    }
  }

  &__link {
    $fz: 14;
    font-size: em($fz, $fz_base);
    transition: color $ease-main;

    &:hover {
      color: lighten($color-brend, 10%);
    }
  }
}