.button {
  position: relative;
  display: inline-block;
  border: 0;
  background-color: transparent;
  transition: opacity $ease-main;
  cursor: pointer;
  color: $color-brend;
  appearance: none;
  border-radius: 2px;
  padding: em(10 15, $fz_base);
  text-transform: uppercase;
  transition: color $ease-main;
  overflow-y: hidden;
  
  &::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    border: 2px solid;
    border-image-source: linear-gradient(to bottom, #0080ff, #00b7ff);
    border-image-slice: 1;
  }

  &__label {
    $fz: 18;
    font-size: em($fz, $fz_base);
  }

  $b: &;

  &[disabled] {
    opacity: 0.5;
    cursor: not-allowed;
  }

  &:hover {
    color: lighten($color-brend, 5%);
  }
}
