//      "//", ..       css-
$fz_base: 16;
$fz: $fz_base;
// ##########
//    
// ##########
$container-width: 1280; //    
$container-width-sm: 1180; //    
$container-padding: 10; //      
$grid-container-width: 100%; //   
$grid-cols: 12; //    
$grid-gap: 30; //   
$grid-gap-vertical: 30; //     
$breakpoints: (
  'xl': 1920px,
  'lg': 1280px,
  'md': 1024px,
  'sm': 768px,
  'xs': 480px,
);

$padding-left-input: 15;
$height-input: 60;

// ##########
// 
// ##########
$color-basic: black; //    (  body)
$color-placeholder: #777; //    placeholder
$color-input: black; //   input, select  textarea
$color-main: #2252d8; //   
$color-border: #ccc; //    , 

$color-light: hsl(0, 0%, 100%);
$color-brend: hsl(210, 90%, 42%);

// ##########
// Transitions
// ##########
$ease-main: 0.2s ease;

// ##########
// 
// ##########
$font-main: 'Bliss Pro', Arial, sans-serif; //   
$font-second: 'Bebas Neue', sans-serif; //  
$font-third: 'RobotoSlab', sans-serif; //  
